import Head from 'next/head'
import React, { Component } from 'react'
import {withAuthSync} from '../../actions/auth'
import {connect} from 'react-redux'
import Header from './Header'
import Navbar from './Navbar'
import Select2 from 'react-select2-wrapper'
import {bindActionCreators} from 'redux'
import { set_process, set_candidate } from '../../actions/global'
import PropTypes from "prop-types";


class Layout extends Component {
    static propTypes = {
        set_process: PropTypes.func.isRequired,
        set_candidate: PropTypes.func.isRequired,
        Process: PropTypes.array,
        SelectProcess: PropTypes.array,
        SelectCandidates: PropTypes.array,
        active_process: PropTypes.number,
        active_candidate: PropTypes.number
    };

    selects = {
        process: this.props.active_process,
        candidate: this.props.active_candidate
    };

    constructor(props){
        super(props);
        this.onChangeProcess = this.onChangeProcess.bind(this);
        this.onChangeCandidate = this.onChangeCandidate.bind(this);
    }

    onChangeProcess = e =>{
        this.selects.process = e.target.value;
        this.props.set_process(this.selects.process);
    };

    onChangeCandidate = e =>{
        this.selects.candidate = e.target.value;
        this.props.set_candidate(this.selects.candidate);
    };

    render () {
        const { breadcrumb, children, SelectProcess, SelectCandidates} = this.props;
        const { process, candidate } = this.selects;
        return (
            <div>
                <Head>
                    <title>Sistema Electoral</title>
                </Head>
                <div className="wrapper">
                    <div className="art-bg">
                        <img src="/static/img/art1.svg" alt="" className="art-img light-img" />
                        <img src="/static/img/art2.svg" alt="" className="art-img dark-img" />
                    </div>
                    <Header {...this.props} />
                    <Navbar {...this.props} />
                    <div className="content-wrapper">
                        <div className="container-full">
                            <div className="content-header">
                                <div className="d-flex align-items-center">
                                    <div className="mr-auto">
                                        <h3 className="page-title br-0">{breadcrumb}</h3>
                                    </div>
                                    <div className="right-title mr-15">
                                        <Select2
                                            className="form-control"
                                            data={SelectProcess}
                                            onChange={this.onChangeProcess}
                                            value={process}
                                        />
                                    </div>
                                    <div className="right-title">
                                        <Select2
                                            className="form-control"
                                            data={SelectCandidates}
                                            onChange={this.onChangeCandidate}
                                            value={candidate}
                                        />
                                    </div>
                                </div>
                            </div>
                            <section className="content">
                                {children}
                            </section>
                        </div>
                    </div>

                    <footer className="main-footer">
                        <div className="pull-right d-none d-sm-inline-block">
                            <ul className="nav nav-primary nav-dotted nav-dot-separated justify-content-center justify-content-md-end">
                                <li className="nav-item">
                                    <a className="nav-link" href="javascript:void(0)">FAQ</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#">Purchase Now</a>
                                </li>
                            </ul>
                        </div>
                        &copy; 2019 <b>Sistema Electoral</b>.
                    </footer>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    Process: state.global.Process,
    SelectProcess: state.global.SelectProcess,
    SelectCandidates: state.global.SelectCandidates,
    active_process: state.global.active_process,
    active_candidate: state.global.active_candidate
});

const mapDispatchToProps = dispatch => {
    return {
        set_process: bindActionCreators(set_process, dispatch),
        set_candidate: bindActionCreators(set_candidate, dispatch)
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(withAuthSync(Layout))