import React, { Component } from 'react'
import Link from './Link'

class Menu extends Component{
    constructor(props){
        super(props)
    }

    render(){
        const { menu } = this.props;
        return(
            <li>
                <Link activeClassName='current' href={menu.href}>
                    <a><i className={`${menu.icon} mx-5`}/>{menu.name}</a>
                </Link>
            </li>
        )
    }
}

export default Menu;