import React, {Component} from 'react'
import DataTable from 'react-data-table-component'
import { connect } from 'react-redux'
import {get_persons, set_formPromoted, save_promoted} from '../../actions/promovido'
import { Checkbox } from 'react-md'
import PropTypes from 'prop-types'
import {bindActionCreators} from 'redux'


class Table extends Component {
    static propTypes = {
        get_persons: PropTypes.func.isRequired,
        set_formPromoted: PropTypes.func.isRequired,
        save_promoted: PropTypes.func.isRequired,
        Data: PropTypes.array,
        Persons: PropTypes.array,
        forms: PropTypes.object,
        formPromoted: PropTypes.object,
        state: PropTypes.object
    };

    constructor(props){
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this)
        const Strings = {};
        Strings.orEmpty = function( entity ) {
            return entity || "";
        };
    }

    onChange = e => {
        e.preventDefault();
        this.props.set_formPromoted(e.target.name, e.target.value.toUpperCase());
        e.target.value = this.props.formPromoted[e.target.name];
        if(e.target.name == 'last_name' || e.target.name == 'second_last_name' || e.target.name == 'name')
        {
            const Strings = {};
            Strings.orEmpty = function( entity ) {
                return entity || "";
            };
            const {last_name, second_last_name, name} = this.props.formPromoted;
            const full = `${Strings.orEmpty( last_name )} ${Strings.orEmpty( second_last_name )} ${Strings.orEmpty( name )}`;
            this.props.get_persons(this.props.Data, this.props.token, this.props.forms, full);
        }
    };

    handleChange = e => {
        if(Array.isArray(e.selectedRows)) {
            if (e.selectedRows.length > 0) {
                let data = e.selectedRows[0];
                let date = data.date.split('/');
                this.props.set_formPromoted('name', data.name);
                this.props.set_formPromoted('last_name', data.last_name);
                this.props.set_formPromoted('second_last_name', data.second_last_name);
                this.props.set_formPromoted('day', date[2]);
                this.props.set_formPromoted('month', date[1]);
                this.props.set_formPromoted('year', date[0]);
                this.props.set_formPromoted('userExist', data.id);
                this.forceUpdate();
            }else{
                this.props.set_formPromoted('userExist', null);
            }
        }
    };

    onSubmit(e) {
        e.preventDefault();
        this.props.save_promoted(this.props.state);
        this.forceUpdate();
    };

    render() {
        const {name, last_name, second_last_name, day, month, year, phone} = this.props.formPromoted;
        return (
            <div className="row">
                <div className="col-7">
                    <div className="box">
                        <div className="box-header with-border">
                            <h4 className="box-title">Personas</h4>
                            <ul className="box-controls pull-right">
                                <li><a className="box-btn-slide" href="#"/></li>
                            </ul>
                        </div>
                        <div className="box-body">
                            <div className="row">
                                <DataTable
                                    noHeader={true}
                                    columns={[
                                        {
                                            name: 'Nombre',
                                            selector: 'full',
                                            sortable: true
                                        },
                                        {
                                            name: 'Fecha Nacimiento',
                                            selector: 'date',
                                            sortable: true
                                        }
                                    ]}
                                    data={this.props.Persons}
                                    style={{height:'500px'}}
                                    noDataComponent="PERSONA NO ENCONTRADA"
                                    selectableRows
                                    selectableRowsComponent={Checkbox}
                                    selectableRowsComponentProps={{ uncheckedIcon: '', checkedIcon: '' }}
                                    onTableUpdate={this.handleChange}
                                />
                            </div>
                        </div>
                    </div>
                    <style jsx>{`
                        .box-body{
                            height: 600px;
                        }
                    `}</style>
                </div>
                <div className="col-5">
                    <div className="box">
                        <div className="box-header with-border">
                            <h4 className="box-title">Promovido</h4>
                            <ul className="box-controls pull-right">
                                <li><a className="box-btn-slide" href="#"/></li>
                            </ul>
                        </div>
                        <div className="box-body pb-0">
                            <div className="row">
                                <form className="col-12" onSubmit={this.onSubmit}>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label>Paterno</label>
                                            <input
                                                className="form-control"
                                                name="last_name"
                                                onChange={this.onChange}
                                                value={last_name}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label>Materno</label>
                                            <input
                                                className="form-control"
                                                name="second_last_name"
                                                onChange={this.onChange}
                                                value={second_last_name}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label>Nombre(s)</label>
                                            <input
                                                className="form-control"
                                                name="name"
                                                onChange={this.onChange}
                                                value={name}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label>Fecha Namicimiento</label>
                                            <div className="row col-12">
                                                <input
                                                    className="form-control col-4"
                                                    name="day"
                                                    onChange={this.onChange}
                                                    value={day}
                                                />
                                                <input
                                                    className="form-control col-4"
                                                    name="month"
                                                    onChange={this.onChange}
                                                    value={month}
                                                />
                                                <input
                                                    className="form-control col-4"
                                                    name="year"
                                                    onChange={this.onChange}
                                                    value={year}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <label>Telefono</label>
                                            <input
                                                className="form-control"
                                                name="phone"
                                                onChange={this.onChange}
                                                value={phone}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-12 text-center">
                                        <button type="submit" className="btn btn-success btn-rounded mb-20 form-control">Guardar
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    Persons: state.promovido.Persons,
    Data: state.promovido.Data,
    token: state.auth.token,
    forms: state.promovido.forms,
    formPromoted: state.promovido.formPromoted,
    state
});

const mapDispatchToProps = dispatch => {
    return {
        get_persons: bindActionCreators(get_persons, dispatch),
        set_formPromoted: bindActionCreators(set_formPromoted, dispatch),
        save_promoted:bindActionCreators(save_promoted, dispatch),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Table);