import React, {Component} from 'react'
import Layout from '../components/Layout'
import Table from '../components/Promovidos/Table'
import Filter from '../components/Promovidos/Filter'
import {connect} from 'react-redux'
import { withAuthSync } from '../actions/auth'
import {Checkbox} from 'react-md'
import DataTable from 'react-data-table-component'


class Promovido extends Component {

    constructor(props){
        super(props);
    }


    render() {
        return (
            <Layout breadcrumb='Nuevo Promovido' {...this.props}>
                <Filter />
                <Table />
                <div className="row">
                    <div className="col-12">
                        <div className="box">
                            <div className="box-header with-border">
                                <h4 className="box-title">Promovidos</h4>
                                <ul className="box-controls pull-right">
                                    <li><a className="box-btn-slide" href="#"/></li>
                                </ul>
                            </div>
                            <div className="box-body">
                                <DataTable
                                    noHeader={true}
                                    columns={[
                                        {
                                            name: 'id',
                                            selector: 'id',
                                            sortable: true
                                        },
                                        {
                                            name: 'Nombre',
                                            selector: 'name',
                                            sortable: true
                                        },
                                        {
                                            name: 'Telefono',
                                            selector: 'phone',
                                            sortable: true
                                        }
                                    ]}
                                    data={this.props.Promoted}
                                    noDataComponent="SIN PROMOVIDOS"
                                    pagination={true}
                                    selectableRows
                                    selectableRowsComponent={Checkbox}
                                    selectableRowsComponentProps={{ uncheckedIcon: '', checkedIcon: '' }}
                                    onTableUpdate={this.handleChange}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}

const mapStateToProps = state => ({
    Promoted: state.promovido.Promoted
});

export default connect(mapStateToProps)(withAuthSync(Promovido));