import React, { Component } from 'react';
import Link from 'next/link';
import {logout, withAuthSync} from '../../../actions/auth'
import {connect} from 'react-redux'
import {bindActionCreators} from "redux";
import PropTypes from "prop-types";


class Header extends Component {
    static propTypes = {
        logout: PropTypes.func.isRequired
    };

    constructor(props){
        super(props);
        this.closeSession = this.closeSession.bind(this)
    }

    closeSession () {
        this.props.logout(this.props.token)
    }

    render(){
        return (
            <header className="main-header">
                <div className="inside-header">
                    <Link href="/">
                        <a className="logo">
                            <div className="logo-mini">
                                <span className="light-logo"><img src="/static/img/logo-light.png" alt="logo" /></span>
                                <span className="dark-logo"><img src="/static/img/logo-dark.png" alt="logo" /></span>
                            </div>

                            <div className="logo-lg">
                                <span className="light-logo"><img src="/static/img/logo-light-text.png" alt="logo" /></span>
                                <span className="dark-logo"><img src="/static/img/logo-dark-text.png" alt="logo" /></span>
                            </div>
                        </a>
                    </Link>


                    <nav className="navbar navbar-static-top">
                        <div>
                            <a id="toggle_res_search" data-toggle="collapse" data-target="#search_form"
                               className="res-only-view">
                                <i className="mdi mdi-magnify"/>
                            </a>

                            <form id="search_form" role="search" className="top-nav-search pull-left collapse ml-20">
                                <div className="input-group">
                                    <input type="text" name="example-input1-group2" className="form-control"
                                           placeholder="Search" />
                                    <span className="input-group-btn">
                                        <button type="button" className="btn  btn-default" data-target="#search_form"
                                                data-toggle="collapse" aria-label="Close" aria-expanded="true">
                                            < i className="mdi mdi-magnify" />
                                        </button>
                                    </span>
                                </div>
                            </form>
                        </div>

                        <div className="navbar-custom-menu r-side">
                            <ul className="nav navbar-nav">

                                <li className="full-screen-btn">
                                    <a data-provide="fullscreen" title="Full Screen">
                                        <i className="mdi mdi-crop-free" />
                                    </a>
                                </li>

                                <li className="dropdown user user-menu">
                                    <a href="#" className="dropdown-toggle" data-toggle="dropdown" title="User">
                                        <img src="/static/img/avatar.jpg" className="user-image rounded-circle"
                                             alt="User Image" />
                                    </a>
                                    <ul className="dropdown-menu animated flipInX">
                                        <li className="user-header bg-img"
                                            style={{ backgroundImage: "url('/static/img/user-info.jpg')" }} data-overlay="3">
                                            <div className="flexbox align-self-center">
                                                <img src="/static/img/avatar.jpg" className="float-left rounded-circle"
                                                     alt="User Image" />
                                                <h4 className="user-name align-self-center">
                                                    <span>Samuel Brus</span>
                                                    <small>samuel@gmail.com</small>
                                                </h4>
                                            </div>
                                        </li>
                                        <li className="user-body">
                                            <Link href="">
                                                <a className="dropdown-item"><i className="ion ion-person"/> My Profile </a>
                                            </Link>
                                            <Link href="">
                                                <a className="dropdown-item"><i className="ion ion-bag"/> My Balance </a>
                                            </Link>
                                            <div className="dropdown-divider"/>
                                            <a href="#" onClick={this.closeSession} className="dropdown-item">
                                                <i className="ion-log-out"/> Logout
                                            </a>
                                        </li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            </header>
        )
    };
}

const mapDispatchToProps = dispatch => {
    return {
        logout: bindActionCreators(logout, dispatch)
    }
};

export default connect(null, mapDispatchToProps)(withAuthSync(Header))