import React from 'react'
import { actionTypes } from './types'

export const get_process = data => dispatch =>{
    dispatch({
        type:actionTypes.GLOBAL_GET_NEW_PROCESS,
        payload: data
    })
};

export const get_candidates = data => dispatch =>{
    dispatch({
        type:actionTypes.GLOBAL_GET_CANDIDATES,
        payload: data
    })
};

export const set_process = data => dispatch => {
    dispatch({
        type: actionTypes.GLOBAL_SET_PROCESS,
        payload: data
    })
};

export const set_candidate = data => dispatch => {
    dispatch({
        type: actionTypes.GLOBAL_SET_CANDIDATE,
        payload: data
    })
};